import VueRouter from 'vue-router';
import Vue from 'vue';
Vue.use(VueRouter)
const routes = [
  {
    path: '/port_analysis',
    name: 'port_analysis',
    component: () => import('../components/port_analysis')
  },
  {
    path: '/test',
    name: 'test',
    component: () => import('../components/test')
  },
  {
    path: '/chart',
    name: 'chart',
    component: () => import('../components/chart')
  }
]

const router = new VueRouter({
    mode:'hash',
    routes
})

export default router;
