<template>
  <div id="app">
    <KeepAlive>
      <router-view></router-view>
    </KeepAlive>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'App',
  components: {
  },
  mounted() {
    this.$router.push('/port_analysis')
    // 请求数据存入sessionStorage
    axios.get('http://127.0.0.1:8000/railway/Get_Busyness_Index/').then(res => {
      sessionStorage.setItem('busynessIndex', JSON.stringify(res.data))
      // 对原始数据进行根据Number_positive_lines字段分组，分为单线，双线，分组后根据Busyness_index字段值进行排序，然后用sessionStorage存储
      let busynessIndex = JSON.parse(sessionStorage.getItem('busynessIndex')).filter(item => item.Interval !== '会龙-仓山镇')
      console.log('ssss',busynessIndex);
      let singleLine = busynessIndex.filter(item => item.Number_positive_lines === '单线')
      let doubleLine = busynessIndex.filter(item => item.Number_positive_lines === '双线')
      singleLine.sort((a, b) => {
        return b.Busyness_index - a.Busyness_index
      })
      doubleLine.sort((a, b) => {
        return b.Busyness_index - a.Busyness_index
      })
      sessionStorage.setItem('singleLine', JSON.stringify(singleLine))
      sessionStorage.setItem('doubleLine', JSON.stringify(doubleLine))
      // 同时对原始数据根据Busyness_index字段值进行排序，然后用sessionStorage存储
      busynessIndex.sort((a, b) => {
        return b.Busyness_index - a.Busyness_index
      })
      sessionStorage.setItem('sortedBusynessIndex', JSON.stringify(busynessIndex))
      
    })
    // 从http://127.0.0.1:8000/railway/Get_Trian_data/请求列车数量数据存入sessionStorage。
    axios.get('http://127.0.0.1:8000/railway/Get_Trian_data/').then(res => {
      sessionStorage.setItem('trainData', JSON.stringify(res.data))     
    })
    axios.get('http://127.0.0.1:8000/railway/Get_Station_Frequency/').then(res => {
      sessionStorage.setItem('stationFrequency', JSON.stringify(res.data))     
    })
    axios.get('http://127.0.0.1:8000/railway/Get_Interval_Data/').then(res => {
      sessionStorage.setItem('intervalData', JSON.stringify(res.data))     
    })
    axios.get('http://127.0.0.1:8000/railway/Get_Interval_run_time/').then(res => {
      sessionStorage.setItem('intervalRuntime', JSON.stringify(res.data))     
    })
    
  },
}
</script>

<style>
#app {
  height: 100%;
}
</style>
